import { Box, Button, FormControlLabel, Switch, TextField, Typography } from '@mui/material'
import { useState } from 'react'
import { useNavigate } from 'react-router-dom'

import usePostCreateBrand from '@/hooks/shopping/usePostCreateBrand'

const BrandCreatePage = () => {
  const navigate = useNavigate()
  const { mutateAsync: createBrand } = usePostCreateBrand()

  const [brandData, setBrandData] = useState({
    name: '',
    description: '',
    logo: '',
    website: '',
    isActive: true
  })

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value, checked } = e.target
    setBrandData({
      ...brandData,
      [name]: name === 'isActive' ? checked : value
    })
  }

  const handleSubmit = async () => {
    try {
      await createBrand(brandData)
      alert('  .')
      navigate(-1)
    } catch (error) {
      alert('    .')
    }
  }

  return (
    <Box sx={{ padding: '20px', display: 'flex', flexDirection: 'column', gap: '15px' }}>
      <Typography variant="h4"> </Typography>

      <TextField
        label=""
        name="name"
        value={brandData.name}
        onChange={handleChange}
        fullWidth
        required
      />

      <TextField
        label=""
        name="description"
        value={brandData.description}
        onChange={handleChange}
        multiline
        rows={4}
        fullWidth
      />

      <TextField
        label=" URL"
        name="logo"
        value={brandData.logo}
        onChange={handleChange}
        fullWidth
      />

      <TextField
        label=""
        name="website"
        value={brandData.website}
        onChange={handleChange}
        fullWidth
      />

      <FormControlLabel
        control={
          <Switch
            checked={brandData.isActive}
            onChange={handleChange}
            name="isActive"
          />
        }
        label=" "
      />

      <Button
        variant="contained"
        color="primary"
        onClick={handleSubmit}
      >
         
      </Button>
    </Box>
  )
}

export default BrandCreatePage
