import { Button, Checkbox, FormControlLabel, TextField } from '@mui/material'
import { useEffect, useState } from 'react'
import { useNavigate, useParams } from 'react-router-dom'

import useFestivalById from '@/hooks/festival/useFestivalById'
import usePatchFestival from '@/hooks/festival/usePatchFestival'

const FestivalRevisePage = () => {
  const { id } = useParams()
  const { data: festival } = useFestivalById(Number(id))
  const { mutate: patchFestival } = usePatchFestival(Number(id))
  const navigate = useNavigate()

  const [formData, setFormData] = useState({
    location: '',
    locationLocal: '',
    specificLocation: '',
    nation: '',
    city: '',
    cityLocal: '',
    subCity: '',
    subCityLocal: '',
    longitude: '',
    latitude: '',
    year: new Date().getFullYear(),
    title: '',
    description: '',
    tag: '',
    summary: '',
    participationFee: 0,
    visible: false,
    reservationAvaliable: false,
    reservationType: 'SINGLE',
    openedAt: '',
    expiredAt: ''
  })

  useEffect(() => {
    if (festival) {
      setFormData({
        ...festival,
        openedAt: festival.openedAt.split('T')[0], //   
        expiredAt: festival.expiredAt.split('T')[0]
      })
    }
  }, [festival])

  //   
  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    })
  }

  //   
  const handleCheckboxChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.checked
    })
  }

  //   
  const handleSubmit = () => {
    const updatedFestivalData = {
      ...formData,
      openedAt: new Date(formData.openedAt), //  Date  
      expiredAt: new Date(formData.expiredAt)
    }
    patchFestival(updatedFestivalData, { onSuccess: () => navigate('/festival', { replace: true }) })
  }

  return (
    <div style={{ padding: '20px' }}>
      <h1>  </h1>
      <div style={{ display: 'flex', flexDirection: 'column', gap: '10px' }}>
        <TextField
          label=""
          name="title"
          value={formData.title}
          onChange={handleChange}
          fullWidth />
        <TextField
          label=" "
          name="location"
          value={formData.location}
          onChange={handleChange}
          fullWidth />
        <TextField
          label=" "
          name="locationLocal"
          value={formData.locationLocal}
          onChange={handleChange}
          fullWidth />
        <TextField
          label=" "
          name="specificLocation"
          value={formData.specificLocation}
          onChange={handleChange}
          fullWidth />
        <TextField
          label=""
          name="nation"
          value={formData.nation}
          onChange={handleChange}
          fullWidth />
        <TextField
          label=" ()"
          name="city"
          value={formData.city}
          onChange={handleChange}
          fullWidth />
        <TextField
          label=" ()"
          name="cityLocal"
          value={formData.cityLocal}
          onChange={handleChange}
          fullWidth />
        <TextField
          label=" ()"
          name="subCity"
          value={formData.subCity}
          onChange={handleChange}
          fullWidth />
        <TextField
          label=" ()"
          name="subCityLocal"
          value={formData.subCityLocal}
          onChange={handleChange}
          fullWidth />
        <TextField
          label=""
          name="longitude"
          value={formData.longitude}
          onChange={handleChange}
          fullWidth />
        <TextField
          label=""
          name="latitude"
          value={formData.latitude}
          onChange={handleChange}
          fullWidth />
        <TextField
          label=""
          name="year"
          value={formData.year}
          onChange={handleChange}
          fullWidth />
        <TextField
          label=""
          name="description"
          value={formData.description}
          onChange={handleChange}
          fullWidth
          multiline
          rows={3} />
        <TextField
          label=""
          name="tag"
          value={formData.tag}
          onChange={handleChange}
          fullWidth />
        <TextField
          label=""
          name="summary"
          value={formData.summary}
          onChange={handleChange}
          fullWidth />
        <TextField
          label=""
          type="number"
          name="participationFee"
          value={formData.participationFee}
          onChange={handleChange}
          fullWidth />
        <TextField
          label=""
          type="date"
          name="openedAt"
          value={formData.openedAt}
          onChange={handleChange}
          fullWidth
          InputLabelProps={{ shrink: true }} />
        <TextField
          label=""
          type="date"
          name="expiredAt"
          value={formData.expiredAt}
          onChange={handleChange}
          fullWidth
          InputLabelProps={{ shrink: true }} />

        <FormControlLabel
          control={<Checkbox
            checked={formData.visible}
            name="visible"
            onChange={handleCheckboxChange} />}
          label=" "
        />
        <FormControlLabel
          control={<Checkbox
            checked={formData.reservationAvaliable}
            name="reservationAvaliable"
            onChange={handleCheckboxChange} />}
          label="  "
        />
        <TextField
          label=" "
          name="reservationType"
          value={formData.reservationType}
          onChange={handleChange}
          fullWidth />

        <Button
          variant="contained"
          color="primary"
          onClick={handleSubmit}>
           
        </Button>
      </div>
    </div>
  )
}

export default FestivalRevisePage
