import DeleteIcon from '@mui/icons-material/Delete'
import { Box, Button, Card, CardActionArea, CardMedia, CircularProgress, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, Divider, Grid, IconButton, List, ListItem, ListItemText, Paper, Typography } from '@mui/material'
import { useState } from 'react'
import { useNavigate, useParams } from 'react-router-dom'

import { ParticipatorModel } from '@/apis/festival/getFestival'
import useDeleteFacility from '@/hooks/festival/useDeleteFacility'
import useDeleteFestivalImage from '@/hooks/festival/useDeleteImage'
import useDeleteParticipator from '@/hooks/festival/useDeleteParticipator'
import useFestivalById from '@/hooks/festival/useFestivalById'

const FestivalDetailPage = () => {
  const { id } = useParams()
  const { data: festival, isLoading } = useFestivalById(Number(id))
  const navigate = useNavigate()
  const { mutate: deleteParticipator } = useDeleteParticipator()
  const { mutate: deleteFestivalImage } = useDeleteFestivalImage()
  const { mutate: deleteFacility } = useDeleteFacility()
  const [selectedImage, setSelectedImage] = useState<string | null>(null)

  //  : Dialog( )     
  const [open, setOpen] = useState(false)
  const [selectedParticipator, setSelectedParticipator] = useState<ParticipatorModel | null>()

  // Dialog 
  const handleOpen = (participator: ParticipatorModel) => {
    setSelectedParticipator(participator)
    setOpen(true)
  }

  // Dialog 
  const handleClose = () => {
    setOpen(false)
    setSelectedParticipator(null)
  }

  //   
  const handleDelete = () => {
    if (selectedParticipator) {
      deleteParticipator({ festivalId: Number(id), participatorId: selectedParticipator.id })
    }
    handleClose()
  }
  // const handleImageClick = (image: string) => {
  //   setSelectedImage(image)
  // }

  const handleCloseImageDialog = () => {
    setSelectedImage(null)
  }

  if (isLoading) {
    return <CircularProgress />
  }

  if (!festival) {
    return <Typography variant="h6">   .</Typography>
  }

  return (
    <Box sx={{ padding: 3 }}>
      <Box>
        <Typography
          variant="h3"
          gutterBottom>
          {festival.title} ({festival.year})
        </Typography>
        <Button
          variant="contained"
          sx={{ backgroundColor: '#01c897', color: 'white' }}
          onClick={() => navigate('revise')}
        >
          
        </Button>
      </Box>
      <Grid
        container
        spacing={2}>
        <Grid
          item
          xs={12}
          md={6}>
          <Paper
            elevation={3}
            sx={{ padding: 2 }}>
            <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginTop: 3 }}>
              <Typography
                variant="h6"
                gutterBottom> </Typography>
            </Box>
            <Typography><strong>:</strong> {festival.tag}</Typography>
            <Typography><strong>:</strong> {festival.description}</Typography>
            <Typography><strong>:</strong> {festival.summary}</Typography>
            <Typography><strong>:</strong> {festival.participationFee}</Typography>
            <Typography><strong>  :</strong> {festival.reservationAvaliable ? '' : ''}</Typography>
            <Typography><strong> :</strong> {festival.reservationType}</Typography>
          </Paper>
        </Grid>
        <Grid
          item
          xs={12}
          md={6}>
          <Paper
            elevation={3}
            sx={{ padding: 2 }}>
            <Typography
              variant="h6"
              gutterBottom> </Typography>
            <Typography><strong>:</strong> {festival.location} ({festival.locationLocal}, {festival.specificLocation})</Typography>
            <Typography><strong>:</strong> {festival.nation}</Typography>
            <Typography><strong>:</strong> {festival.city} ({festival.cityLocal})</Typography>
            <Typography><strong>:</strong> {festival.subCity} ({festival.subCityLocal})</Typography>
            <Typography><strong>:</strong> {festival.latitude}, {festival.longitude}</Typography>
            <Typography><strong> :</strong> {festival.visible ? '' : ''}</Typography>
            <Typography><strong> :</strong> {new Date(festival.openedAt).toLocaleDateString()} ~ {new Date(festival.expiredAt).toLocaleDateString()}</Typography>
          </Paper>
        </Grid>
      </Grid>
      <h1></h1>
      <Button
        variant="contained"
        sx={{ backgroundColor: '#01c897', color: 'white' }}
        onClick={() => navigate('facility/create')}
      >
          
      </Button>
      {festival.facilities && festival.facilities.length > 0 ? (
        <List sx={{ marginTop: 2 }}>
          {festival.facilities.map((facility) => (
            <Paper
              key={facility.id}
              elevation={2}
              sx={{ marginBottom: 2 }}>
              <ListItem>
                <ListItemText
                  primary={`${facility.name} (${facility.type})`}
                  secondary={
                    <Typography
                      component="span"
                      variant="body2"
                      sx={{ fontStyle: 'italic' }}>
                      {facility.description}
                    </Typography>
                  }
                />
                <IconButton
                  edge="end"
                  aria-label="delete"
                  onClick={() => deleteFacility({ facilityId: facility.id, festivalId: festival.id })} //  
                >
                  <DeleteIcon />
                </IconButton>
              </ListItem>
              <Divider />
            </Paper>
          ))}
        </List>
      ) : (
        <Typography sx={{ marginTop: 2 }}> .</Typography>
      )}
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginTop: 3 }}>
        <Typography variant="h5"> </Typography>
        <Button
          variant="contained"
          sx={{ backgroundColor: '#01c897', color: 'white' }}
          onClick={() => alert(' ')}
        >
           
        </Button>
      </Box>
      <Grid
        container
        spacing={2}
        sx={{ marginTop: 2 }}>
        {festival.images.map((image, index) => (
          <Grid
            item
            xs={12}
            sm={6}
            md={4}
            lg={3}
            key={index}>
            <Card>
              <CardActionArea>
                <CardMedia
                  component="img"
                  height="200"
                  image={image.image}
                  alt={`Festival image ${index + 1}`}
                />
                <IconButton
                  sx={{
                    position: 'absolute',
                    top: 8,
                    right: 8,
                    backgroundColor: 'rgba(255, 255, 255, 0.7)',
                    '&:hover': { backgroundColor: 'rgba(255, 0, 0, 0.7)' } //    
                  }}
                  onClick={() => deleteFestivalImage({ festivalId: Number(id), imageId: image.id })}
                >
                  <DeleteIcon sx={{ color: '#f44336' }} /> {/*    */}
                </IconButton>
              </CardActionArea>
            </Card>
          </Grid>
        ))}
      </Grid>
      {selectedImage && (
        <Dialog
          open={Boolean(selectedImage)}
          onClose={handleCloseImageDialog}
          maxWidth="md"
        >
          <DialogTitle> </DialogTitle>
          <DialogContent>
            <img
              src={selectedImage}
              alt="Selected Festival Image"
              style={{ width: '100%', height: 'auto' }}
            />
          </DialogContent>
          <DialogActions>
            <Button
              onClick={handleCloseImageDialog}
              color="primary">
              
            </Button>
          </DialogActions>
        </Dialog>
      )}
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginTop: 3 }}>
        <Typography variant="h5">  </Typography>
        <Button
          variant="contained"
          sx={{ backgroundColor: '#01c897', color: 'white' }}
          onClick={() => navigate('participator/create')}
        >
           
        </Button>
      </Box>

      {festival.participators && festival.participators.length > 0 ? (
        <List sx={{ marginTop: 2 }}>
          {festival.participators
            .slice() //      slice 
            .sort((a, b) => b.boothNumber - a.boothNumber) // boothNumber   
            .map((participator) => (
              <Paper
                key={participator.id}
                elevation={2}
                sx={{ marginBottom: 2 }}>
                <ListItem
                  secondaryAction={
                    <IconButton
                      edge="end"
                      aria-label="delete"
                      onClick={() => handleOpen(participator)}>
                      <DeleteIcon sx={{ color: '#f44336' }} /> {/*    */}
                    </IconButton>
                  }
                >
                  <ListItemText
                    primary={`${participator.name} ( : ${participator.boothNumber})`}
                    secondary={<Typography
                      component="span"
                      variant="body2"
                      sx={{ fontStyle: 'italic' }}>{participator.description}</Typography>}
                  />
                </ListItem>
                <Divider />
              </Paper>
            ))}
        </List>
      ) : (
        <Typography sx={{ marginTop: 2 }}>  .</Typography>
      )}

      {/*   Dialog */}
      <Dialog
        open={open}
        onClose={handleClose}
      >
        <DialogTitle> </DialogTitle>
        <DialogContent>
          <DialogContentText>
             <strong>{selectedParticipator?.name}</strong>  ?
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button
            onClick={handleClose}
            color="primary"></Button>
          <Button
            onClick={handleDelete}
            color="error"></Button>
        </DialogActions>
      </Dialog>
    </Box>
  )
}

export default FestivalDetailPage
