import { Button, Card, CardContent, CircularProgress, Typography } from '@mui/material'
import dayjs from 'dayjs'
import { useNavigate } from 'react-router-dom'

import useFestivals from '@/hooks/festival/useFestivals'

const FestivalPage = () => {
  const navigate = useNavigate()
  const { data: festivals, isLoading } = useFestivals()

  //    
  const getFestivalStatus = (openedAt: string | null, expiredAt: string | null) => {
    const now = dayjs()
    const openDate = openedAt ? dayjs(openedAt) : null
    const expireDate = expiredAt ? dayjs(expiredAt) : null

    if (openDate && expireDate) {
      if (now.isBefore(openDate)) {
        return ''
      } else if (now.isAfter(expireDate)) {
        return ''
      } else {
        return ' '
      }
    }
    return '  '
  }

  //     
  const formatFestivalPeriod = (openedAt: string | null, expiredAt: string | null) => {
    const openDate = openedAt ? dayjs(openedAt).format('YYYY.MM.DD') : ''
    const expireDate = expiredAt ? dayjs(expiredAt).format('YYYY.MM.DD') : ''
    return `${openDate} ~ ${expireDate}`
  }

  if (isLoading) {
    return <CircularProgress style={{ marginTop: '20px' }} />
  }

  return (
    <div style={{ padding: '20px' }}>
      <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '20px' }}>
        <h1> </h1>
        <Button
          variant="contained"
          style={{ backgroundColor: '#01c897', color: 'white' }}
          onClick={() => navigate('create')}
        >
           
        </Button>
      </div>

      <Typography
        variant="h5"
        style={{ marginBottom: '10px' }}>
          
      </Typography>

      {festivals && festivals.length > 0 ? (
        festivals.map((festival) => {
          const status = getFestivalStatus(festival.openedAt, festival.expiredAt)
          const period = formatFestivalPeriod(festival.openedAt, festival.expiredAt)

          return (
            <div
              key={festival.id}
              onClick={() => navigate(`${festival.id}`)}>
              <Card
                style={{ marginBottom: '15px', padding: '10px' }}>
                <CardContent>
                  <Typography
                    variant="h6"
                    gutterBottom>
                    {festival.title} ({festival.year})
                  </Typography>
                  <Typography
                    variant="body2"
                    color="textSecondary">
                    {festival.description}
                  </Typography>
                  <Typography
                    variant="body1"
                    style={{ marginTop: '10px' }}>
                  : {festival.locationLocal}
                  </Typography>
                  <Typography variant="body1">
                  : {festival.participationFee}
                  </Typography>
                  <Typography
                    variant="body2"
                    style={{ marginTop: '5px' }}>
                    {festival.reservationAvaliable ? ' ' : ' '}
                  </Typography>
                  <Typography
                    variant="body2"
                    style={{ marginTop: '10px', color: '#888' }}>
                  : {period}
                  </Typography>
                  <Typography
                    variant="body2"
                    style={{ marginTop: '5px', color: status === ' ' ? 'green' : status === '' ? 'blue' : 'red' }}>
                  : {status}
                  </Typography>
                </CardContent>
              </Card>
            </div>
          )
        })
      ) : (
        <Typography
          variant="body1"
          color="textSecondary">
            .
        </Typography>
      )}
    </div>
  )
}

export default FestivalPage
